.mapMainContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 25px;
    margin-bottom: 25px;
}

.mapMainContainer iframe {
    border: 0;
    aspect-ratio: 16 / 9;
    border-radius: 20px;
}

.divisor {
    width: 60%;
    height: 5px;
    border-radius: 30px;
    background-color: black;
    margin-top: 10px;
    margin-bottom: 10px;
    margin-right: auto;
    margin-left: auto;
}